.RoomCode {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    /* background: yellowgreen; */
    font-size: 2.5em;
    font-family: 'Josefin Sans', sans-serif;
}

.roomEle {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 50vh;
    /* background: yellow; */
}

.roomCodeProfile {
    width: 48vw;
}

.RoomCode input {
    border: none;
    border-bottom: black solid 1px;
    width: 48vw;
    background: transparent;
    font-size: 0.5em;
    text-align: center;
    font: rgb(198, 197, 197);
    outline: none;
}

.RoomCode button {
    width: 48vw;
    height: 40px;
    background: rgb(52, 184, 56);
    border: transparent;
    color: white;
    border-radius: 5px;
    cursor: pointer;
    font-size: 0.5em;
    outline: none;
}