.Play {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    /* background: yellowgreen; */
    font-size: 2.5em;
    font-family: 'Josefin Sans', sans-serif;
}

.playEle {
    display: flex;
    flex-direction: column;
    height: 90vh;
    justify-content: space-around;
    align-items: center;
    /* background: yellowgreen; */
    font-size: 2.5em;
    font-family: 'Josefin Sans', sans-serif;
}

.Play input {
    height: 30px; 
    width: 55vw;
    outline: none;
    border-radius: 10px;
    border: green solid 2px;
    font-size: 0.5em;
    text-align: center;
}

.Play button {
    font-size: 0.3em;
    background: rgb(52, 184, 56);
    border: transparent;
    color: white;
    border-radius: 100%;
    width: 40px;
    height: 40px;
    cursor: pointer;
    outline: none;
}

.playAnswer {
    width: 80vw;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    font-size: 0.5em; 
}

.album {
    width: 40vw;
    height: auto;
}

.text {
    font-size: 0.2em;
}

.texts {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.time {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 0.4em;
}

.progress {
    width: 80vw;
    height: auto;
}
