.ChooseType {
    font-size: 2.2em;
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.ChooseType button {
    width: 48vw;
    height: 40px;
    background: rgb(52, 184, 56);
    border: transparent;
    color: white;
    border-radius: 5px;
    cursor: pointer;
    font-size: 0.5em;
    outline: none;
}

.ChooseEle {
    height: 30vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}