.scoreButton {
    width: 80vw;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    bottom: 20px;
}

.scoreButton button {
    background: rgb(52, 184, 56);
    border: transparent;
    color: white;
    border-radius: 70px;
    width: 70px;
    height: 70px;
    cursor: pointer;
    outline: none;
}